<template>
    <el-upload
            class="upload-demo"
            ref="vueFileUploader"
            :action = "uploadUrl()"
            :multiple = false
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :file-list="fileList"
            :before-upload="beforeUploadFile"
            :auto-upload="false">
        <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
        <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
        <div slot="tip" class="el-upload__tip">只能上传zip文件，且不超过500M</div>
    </el-upload>
</template>
<script>
    export default {
        data() {
            return {
                fileList: [
                ],
                name : JSON.parse(sessionStorage.getItem('user')).name,
            };
        },
        methods: {
            uploadUrl :function(){
               return "http://gogs.modelica-china.com:8080/api/directory/uploadDirectory?name="+this.$data.name
              //  return "https://jsonplaceholder.typicode.com/posts/"
            },
            submitUpload() {
         //     this.$refs.vueFileUploader.submit;
           //     this.$refs.vueFileUploader.autoUpload = true;
                this.$refs.vueFileUploader.submit();
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            beforeUploadFile(file){
                let fileMAx = 1024 * 1024 *500;
                if(fileMAx < file.size){
                    this.$message({
                        message: '文件过大！',
                        type: 'warning'
                    });
                    abort(file);
                }
                if(!(file.name).endsWith(".zip")){
                    this.$message({
                        message: '请上传压缩文件！',
                        type: 'warning'
                    });
                    abort(file);
                }
                if(FileList.length >1){
                    this.$message({
                        message: '请上传一个文件！',
                        type: 'warning'
                    });
                }
            }


        }
    }
</script>


